import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

var audio = document.getElementById('myAudio');

function playAudio() {
  audio.play();
}

function pauseAudio() {
  audio.pause();
}

function stopAudio() {
  audio.pause();
  audio.currentTime = 0;
}

